import React from 'react'
import { connect } from 'react-redux'

import { 
  Form,  
  Input,
  Radio,
  DatePicker,
} from 'antd'

const formItemLayout = {
	labelCol: {
		xs: { span: 24 },
		sm: { span: 6 },
	},
	wrapperCol: {
		xs: { span: 24 },
		sm: { span: 18 },
	},
};


class Parent extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      address: undefined,
      _phone1: "",
      _phone2: "",
      _phone3: "",
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleChangeDate = this.handleChangeDate.bind(this)
    this.handlePhone = this.handlePhone.bind(this)
    this.handleAddress = this.handleAddress.bind(this)
  }

  handleChange(e) {
    const { name, value } = e.target
    this.setState({ [name]: value }, () => {
      this.props.setData({
        ...this.props.data,
        ...this.state,
      })
    })
  }
  
  handleChangeDate(name, moment) {
    const day = moment.date()
    const month = moment.month() + 1
    const year = moment.year()
    
    this.setState({ [name]: `${year}-${month}-${day}` }, () => {
      this.props.setData({
        ...this.props.data,
        ...this.state,
      })
    })
  }

  handlePhone(e) {
    const { name, value } = e.target
    this.setState({ [name]: value }, () => {
      const { _phone1, _phone2, _phone3 } = this.state
      const phone = _phone1 + _phone2 + _phone3
       this.setState({ phone }, () => {
        this.props.setData({
          ...this.props.data,
          ...this.state,
        })
      })
    })
  }
  
  handleAddress(a) {
    this.setState({ address: a.address }, () => {
       this.props.setData({
        ...this.props.data,
        ...this.state,
      })
      const geocoder = new window.daum.maps.services.Geocoder()
      geocoder.addressSearch(a.address, (res, status) => {
        if (status === window.daum.maps.services.Status.OK) {
          //const coord = new window.daum.maps.LatLng(res[0].y, res[0].x)
          this.props.setData({
            ...this.props.data,
            'addressX': parseFloat(res[0].x), 
            'addressY': parseFloat(res[0].y),
          })
        }
      })
    })
  }

  render() {
    const { 
      getFieldDecorator,
      setFieldsValue,
    } = this.props.form;
    
    return (
      <Form {...formItemLayout} >


        <Form.Item label="보호자 이름" >
          {getFieldDecorator('parentName', {
            rules: [
              { required: true, message: "보호자 이름을 입력해주세요." },
            ]
          })( <Input name="parentName" onChange={this.handleChange}/> )}
        </Form.Item>
        
        
        <Form.Item label="성별" >
          {getFieldDecorator('parentGender', {
            rules: [
              { required: true, message: "성별을 입력하세요."}
              ]
          })( 
            <Radio.Group name="parentGender" buttonStyle="solid" onChange={this.handleChange}>
              <Radio.Button value="female">F</Radio.Button>
              <Radio.Button value="male">M</Radio.Button>
            </Radio.Group>
          )}
        </Form.Item>
        <Form.Item label="생년월일" >
          {getFieldDecorator('parentBirthDate', {
            rules: [
              { type: 'object', required: true, message: "생년월일을 입력하세요."}
            ]
          })( <DatePicker onChange={m => this.handleChangeDate("parentBirthDate", m)}/> )}
        </Form.Item>
        <Form.Item label="전화번호">
          {getFieldDecorator('phone', {
            rules: [
              { required: true, message: "전화번호를 입력해주세요." },
            ]
          })( 
            <Input.Group>
              <Input style={{width: '20%'}} maxLength={3} name="_phone1" onChange={this.handlePhone}/> 
              <Input style={{width: '40%'}} maxLength={4} name="_phone2" onChange={this.handlePhone}/>
              <Input style={{width: '40%'}} maxLength={4} name="_phone3" onChange={this.handlePhone}/>
            </Input.Group>
          )}
        </Form.Item>
        <Form.Item label="주소">
           {getFieldDecorator('address', {
            rules: [
              { required: true, message: "주소를 입력해주세요." },
            ]
          })( 
          <Input name="address" 
            onClick={() => {
              new window.daum.Postcode({
                oncomplete: (data) => {
                  this.handleAddress(data)
                  setFieldsValue({ 'address': data.address })
               }
              }).open()
            }}/> )}
        </Form.Item>
       <Form.Item label="상세주소">
           {getFieldDecorator('addressDetail', {
            rules: [
              { required: true, message: "상세주소를 입력해주세요." },
            ]
          })( 
          <Input name="addressDetail" onChange={this.handleChange} /> )}
        </Form.Item>
      </Form>
    )
  }
}

const WrappedParentForm = Form.create({ name: 'parent' })(Parent);

const mapStateToProps = (state) => {
  // TBI : initialValue 추가
  return {
    
  }
}

const connectedParent = connect(mapStateToProps, {
})(WrappedParentForm)

export { connectedParent as Parent }
