import React from 'react'
import styles from './App.module.scss'
import styled from 'styled-components'
import { Router, Route } from 'react-router-dom'
import { connect } from 'react-redux'

import { history } from './_helpers'
import { alertActions } from './_actions'
import { PrivateRoute } from './_components'
import { NavContainer } from './_containers'
import { HomePage } from './HomePage'
import { MainPage } from './MainPage'
import { 
  RegisterPage,
  InstructorRegisterPage,
} from './RegisterPage'
import { LoginPage } from './LoginPage'
import { GroupPage } from './GroupPage'
import { 
  SettingPersonalPage,
  SettingParentPage,
  SettingChildPage,
} from './SettingPage'
import classNames from 'classnames/bind'
import { Alert } from 'antd'

const cx = classNames.bind(styles)

class App extends React.Component {
  constructor(props) {
    super(props)

    const { dispatch } = this.props
    history.listen((/* location, action */) => {
      // clear alert on location change
      dispatch(alertActions.clear())
    })
  }

  render() {
    const { alert } = this.props
    return (
      <div className={cx('wrapper')}>
        {alert.message &&
          <Alert banner message={alert.message} type={alert.type} />
        }
        <Router history={history}>
          <NavContainer className={cx('header')}/>
          <div className={cx('container')}>
            {/*
            <Route exact path="/" component={LandingPage} />
            */}
            <Route exact path="/" component={MainPage} />
            <Route exact path='/login' component={LoginPage} />
            <Route exact path='/register' component={RegisterPage} />
            <Route exact path='/register/instructor' component={InstructorRegisterPage} />
            <Route exact path="/home" component={HomePage} />
            <Route exact path="/group" component={GroupPage} />
            
            <Route exact path='/settings/personal' component={SettingPersonalPage} />
            <Route exact path='/settings/parent' component={SettingParentPage} />
            <Route exact path='/settings/child' component={SettingChildPage} />
          </div>
        </Router>
      </div>
    )
  }
}

function mapStateToProps(state) {
  const { alert } = state
  return {
    alert
  }
}

const connectedApp = connect(mapStateToProps)(App)
export { connectedApp as App }
