import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import classNames from 'classnames/bind'
import moment from 'moment'
import styles from './MainSider.scss'
import { Link } from 'react-router-dom'


import  {
  Avatar,
  Button,
	Comment,
  Divider,
  DatePicker,
} from 'antd'

const cx = classNames.bind(styles)

const getRandomColor = () => {
  var letters = '0123456789ABCDEF';
  var color = '#';
  for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
  return color;
}

const ChildTemplate = ({phone, childName, childAge, childGender}) => {
  return (
		<Comment
      classname={cx('childTemplate')}
			author={childName}
			avatar={
	      <Avatar style={{ backgroundColor: getRandomColor(), verticalAlign: 'middle' }} size="large">
        </Avatar>
			}
			content={
				<p>
          {childAge} / {childGender} 
          <br />
          {phone}
				</p> 
			}
	  />
  )
}

const InstructorTemplate = ({phone, instructorName, instructorAge, instructorGender}) => {
  return (
		<Comment
      classname={cx('instructorTemplate')}
			author={instructorName}
			avatar={
        <Avatar style={{ backgroundColor: getRandomColor(), verticalAlign: 'middle' }} size="large">
        </Avatar>
      }
			content={
				<p>
          {instructorAge} / {instructorGender} 
          <br />
          {phone}
				</p> 
			}
	  />
  )
}

const MainSider = (props) => {
  // 메인 화면에  hover되어 있는 사이드바  
  const [date, setDate] = useState(moment())
  const [Members, setMembers] = useState(() => <div />) 
  const [Place, setPlace] = useState(() => <div />)
  const [Instructor, setInstructor] = useState(() => <div />)

  useEffect(() => {
    const d = date.diff(moment().startOf('day'), 'days')
    
    setMembers(
      (d >= 3) ? (
        <div/>
      ) : (
        <React.Fragment>
        <ChildTemplate 
          childName="김서윤"
          childAge="5"
          childGender="여"
          phone='010-1111-2222'
        />
        <ChildTemplate 
          childName="이민준"
          childAge="4"
          childGender="남"
          phone='010-1111-2222'
        />
        <ChildTemplate 
          childName="박수아"
          childAge="4"
          childGender="여"
          phone='010-1111-2222'
        />
        <ChildTemplate 
          childName="최주원"
          childAge="4"
          childGender="남"
          phone='010-1111-2222'
        />
        </React.Fragment>
      )
    )
    setPlace(
      (d > 1) ? (
        <div/>
      ) : (
        <p>
          경기 성남시 분당구 야탑로 20
          <br />
          탑선경아파트 1601호
        </p>
      )
    )
    setInstructor(
      (d > 0) ? (
        <div/>
      ) : (
        <InstructorTemplate 
          instructorName="김유진"
          instructorAge="30"
          instructorGender="여"
          phone='010-1111-2222'
        />
      )
    )
  }, [date])

  const renderDate = (current) => {
    return (
      <p>
        {current.date()}
      </p>
    )
  }

  return (
    <div className={cx('main-sider')}>
      <div className={cx('main-datepicker')}>
        <DatePicker onChange={setDate}
          dateRender={renderDate}
          defaultValue={date}
        />
        <Link to="/settings/personal">
          <Button type="ghost" icon="setting"/>
        </Link>
      </div>

      <div className={cx('main-members')}>
        <h3>그룹 멤버</h3>
        {Members}
     </div>
      <Divider />
      <div className={cx('main-instructor')}>
        <h3>인솔자</h3>
        {Instructor}
     </div>
      <Divider />
      <div className={cx('main-meeting-place')}>
        <h3>모임 장소</h3>
        {Place}
     </div>
    </div>
  )
}

function mapStateToProps(state) { 
  const { match } = state;
  return {
    match,
  }
}

const connectedMainSider = connect(mapStateToProps)(MainSider)
export { connectedMainSider as MainSider }
