import React, {useState} from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { userActions } from '../_actions'
import {
  Steps,
  Row,
  Col,
  Button,
} from 'antd'
import { Personal, Parent, Child, Instructor } from '../_components'

import classNames from 'classnames/bind'
import styles from './RegisterPage.module.scss'

const cx = classNames.bind(styles)

const RegisterPage = (props) => { 

  const [data, setData_] = useState({})
  const [step, setStep] = useState(0)

  const RegisterStep = (step) => (
    <Steps current={step} style={{ padding: "16px", marginBottom: "50px" }}>
      <Steps.Step />
      <Steps.Step />
      <Steps.Step />
    </Steps>
  )

  const setData = (i) => {
    console.log(i)
    setData_(i)
  }

  return (
      <div className={cx('registerWrapper')}>
        <div className={cx('container')}>
          <div className={cx('registerContent')}>
            { RegisterStep(step) }
            { step === 0 && <Personal data={data} setData={setData}/> }
            { step === 1 && <Parent data={data} setData={setData}/> }
            { step === 2 && <Child data={data} setData={setData}/> }
            {/* Back / Continue / Submit buttons */}
            <Row type="flex" >
              <Col span={4}>
                {step > 0 ? (
                  <Button onClick={() => setStep(step - 1)}>
                    뒤로
                  </Button>
                ) : (
                  <Link to="/login">
                    <Button type="danger">
                      뒤로
                    </Button>
                  </Link>
                )}
              </Col>
               <Col span={4}>
                {step < 2 ? (
                  <Button onClick={() => setStep(step + 1)}>
                    다음
                  </Button>
                ) : (
                  <Button type="primary" onClick={() => props.register(data)}>
                    제출
                  </Button>
                )}
              </Col>
           </Row>
          </div>
        </div>
      </div>
  )
}


const InstructorRegisterPage = (props) => { 

  const [data, setData_] = useState({})
  const [step, setStep] = useState(0)

  const RegisterStep = (step) => (
    <Steps current={step} style={{ padding: "16px" }}>
      <Steps.Step />
      <Steps.Step />
    </Steps>
  )

  const setData = (i) => {
    console.log(i)
    setData_(i)
  }

  return (
      <div className={cx('registerWrapper')}>
        <div className={cx('container')}>
          <div className={cx('registerContent')}>
            { RegisterStep(step) }
            { step === 0 && <Personal data={data} setData={setData}/> }
            { step === 1 && <Instructor data={data} setData={setData}/> }
            {/* Back / Continue / Submit buttons */}
            <Row type="flex" >
              <Col span={4}>
                {step > 0 ? (
                  <Button onClick={() => setStep(step - 1)}>
                    뒤로
                  </Button>
                ) : (
                  <Link to="/login">
                    <Button type="danger">
                      뒤로
                    </Button>
                  </Link>
                )}
              </Col>
               <Col span={4}>
                {step < 1 ? (
                  <Button onClick={() => setStep(step + 1)}>
                    다음
                  </Button>
                ) : (
                  <Button type="primary" onClick={() => props.register(data)}>
                    제출
                  </Button>
                )}
              </Col>
           </Row>
          </div>
        </div>
      </div>
  )
}


const mapStateToProps = (state) => { 
  return {}
}

const connectedRegisterPage = connect(mapStateToProps, {
  register: userActions.register,
})(RegisterPage)

const connectedInstructorRegisterPage = connect(mapStateToProps, {
  register: userActions.register,
})(InstructorRegisterPage)

export { 
  connectedRegisterPage as RegisterPage,
  connectedInstructorRegisterPage as InstructorRegisterPage
}
