import React from 'react'
import { connect } from 'react-redux'
import { question_list } from './data'
import { 
  Button,
  Form,  
  Input,
  Modal,
  Radio,
  DatePicker,
} from 'antd'

const formItemLayout = {
	labelCol: {
		xs: { span: 24 },
		sm: { span: 6 },
	},
	wrapperCol: {
		xs: { span: 24 },
		sm: { span: 18 },
	},
};

class Child extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      _modal: false,
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleChangeDate = this.handleChangeDate.bind(this)
    this.handleVisible = this.handleVisible.bind(this)
  }

  handleChange(e) {
    const { name, value } = e.target
    this.setState({ [name]: value }, () => {
      this.props.setData({
        ...this.props.data,
        ...this.state,
      })
    })
  }

  handleVisible(b) {
    this.setState({_modal: b})
  }
  
  handleChangeDate(name, moment) {
    const day = moment.date()
    const month = moment.month() + 1
    const year = moment.year()
    
    this.setState({ [name]: `${year}-${month}-${day}` }, () => {
      this.props.setData({
        ...this.props.data,
        ...this.state,
      })
    })
  }

  render() {
    const { 
      getFieldDecorator,
      setFieldsValue,
    } = this.props.form;
    
    return (
      <Form {...formItemLayout} >
        <Form.Item label="아이 이름" >
          {getFieldDecorator('childName', {
            rules: [
              { required: true, message: "보호자 이름을 입력해주세요." },
            ]
          })( <Input name="childName" onChange={this.handleChange}/> )}
        </Form.Item>
        <Form.Item label="성별" >
          {getFieldDecorator('childGender', {
            rules: [
              { required: true, message: "성별을 입력하세요."}
              ]
          })( 
            <Radio.Group name="childGender" buttonStyle="solid" onChange={this.handleChange}>
              <Radio.Button value="female">F</Radio.Button>
              <Radio.Button value="male">M</Radio.Button>
            </Radio.Group>
          )}
        </Form.Item>
        <Form.Item label="생년월일" >
          {getFieldDecorator('childBirthDate', {
            rules: [
              { type: 'object', required: true, message: "생년월일을 입력하세요."}
            ]
          })( <DatePicker onChange={m => this.handleChangeDate("childBirthDate", m)}/> )}
        </Form.Item>
        <Form.Item label="신장" >
          {getFieldDecorator('childHeight', {
            rules: [
              { required: true, message: "아이의 신장을 입력해주세요." },
            ]
          })( <Input name="childHeight" onChange={this.handleChange}/> )}
        </Form.Item>
        <Form.Item label="체중" >
          {getFieldDecorator('childWeight', {
            rules: [
              { required: true, message: "아이의 체중을 입력해주세요." },
            ]
          })( <Input name="childWeight" onChange={this.handleChange}/> )}
        </Form.Item>
        <Form.Item label="발달단계" >
          {getFieldDecorator('childDevelopmentAge', {
            rules: [
              { required: true, message: "아이의 발달연령을 입력해주세요." },
            ]
          })( 
            <React.Fragment>
             <Radio.Group value={this.state.childDevelopmentAge} 
               name="childDevelopmentAge" buttonStyle="solid" onChange={this.handleChange}>
              <Radio.Button value="2">2세</Radio.Button>
              <Radio.Button value="3">3세</Radio.Button>
              <Radio.Button value="4">4세</Radio.Button>
              <Radio.Button value="5">5세</Radio.Button>
              <Radio.Button value="6">학동기</Radio.Button>
            </Radio.Group>
            <Button type="primary" onClick={() => this.handleVisible(true)}>
              발달연령 테스트
            </Button>
            <Modal 
              title="발달연령 테스트"
              visible={this.state._modal}
              onOk={() => {this.setState({'childDevelopmentAge': '4'}); this.handleVisible(false) }}
              onCancel={() => this.handleVisible(false)}
            >
              {question_list.map((q, idx) => {
                  return (
                    <React.Fragment>
                      <h3>{idx}. {q}</h3>
                      <Radio.Group>
                        <Radio value={1}>네</Radio>
                        <Radio value={0}>아니오</Radio>
                      </Radio.Group>
                    </React.Fragment>
                  )
                })
              }
            </Modal>
            </React.Fragment>
          )}
        </Form.Item>
        <Form.Item label="특이사항" >
          {getFieldDecorator('childRemark', {
          })( <Input name="childRemark" onChange={this.handleChange}/> )}
        </Form.Item>
  
      </Form>
    )
  }
}

const WrappedChildForm = Form.create({ name: 'child' })(Child);

const mapStateToProps = (state) => {
  // TBI : initialValue 추가
  return {
    
  }
}

const connectedChild = connect(mapStateToProps, {
})(WrappedChildForm)

export { connectedChild as Child }
