import React from 'react'
import {
  Form, Icon, Input, Spin, Button,
  Steps, Row, Col,
} from 'antd'
import { userActions } from '../_actions'
import { connect } from 'react-redux'
import classNames from 'classnames/bind'
import styles from './LoginForm.module.scss'
import { Link } from 'react-router-dom'

const cx = classNames.bind(styles)

const { Step } = Steps

class RegisterForm extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      current_step: 0,
      name: '',
      email: '',
      password: '',
      submitted: false
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  handleChange(e) {
    console.log(e)
    const { name, value } = e.target
    this.setState({ [name]: value })
  }

  handleSubmit(e) {
    e.preventDefault()

    this.setState({ submitted: true })
    this.props.form.validateFields((err) => {
      if (!err) {
        const { dispatch } = this.props
        const args = this.state
        if (args) {
          dispatch(userActions.register(args))
        } else {
          alert("cannot dispatch the request: parameter is missing")
        }
      }
    })
  }
  
  compareToFirstPassword(rule, value, callback) {
    const { form } = this.props;
    if (value && value !== form.getFieldValue('password')) {
      callback('Two passwords that you enter is inconsistent!');
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && this.state.confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };


  validate_step_one() {
    
  }

  validate_step_two() {
    
  }

  validate_step_three() {
    
  }

  

  render() {
    const { loggingIn } = this.props
    const { getFieldDecorator } = this.props.form
    return (
      <div>
        <Steps current={this.state.current_step}
          onChange={(step) => this.setState({ current_step: step })}
          style={{ margin: '0 0 12px 0' }}>
          <Step key={1} title="Basic" /> 
          <Step key={2} title="Parent" /> 
          <Step key={3} title="Child" /> 
        </Steps>

        {this.state.current_step === 0 &&
          <Form className={cx('login-form')}>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('email', {
                rules: [{ required: true, message: 'Please input your email!' },
                        { type: "email", message: "The input is not valid E-mail!"}],
                initialValue: this.state.email,
              })(
                <Input name='email'
                  onChange={this.handleChange}
                  prefix={<Icon type='mail' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='이메일' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('password', {
                rules: [{ required: true, message: 'Please input your Password!' },
                       ],
                initialValue: this.state.password,
              })(
                <Input name='password'
                  onChange={this.handleChange}
                  prefix={<Icon type='lock' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  type='password' placeholder='비밀번호' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 12px 0' }}>
              {getFieldDecorator('confirm_password', {
                rules: [{ required: true, message: 'Please input your Password!' },
                        { validator: this.compareToFirstPassword },],
                initialValue: this.state.confirm_password,
              })(
                <Input name='confirm_password'
                  onChange={this.handleChange}
                  prefix={<Icon type='lock' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  type='password' placeholder='비밀번호 확인' />
              )}
            </Form.Item>
          </Form>
        }

        {this.state.current_step === 1 &&
          <Form>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('parent_name', {
                rules: [{ required: true, message: 'Please input your name!' }],
                initialValue: this.state.parent_name,
              })(
                <Input name='parent_name'
                  onChange={this.handleChange}
                  prefix={<Icon type='user' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='보호자 이름' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('parent_gender', {
                rules: [{ required: true, message: 'Please input your gender!' }],
                initialValue: this.state.parent_gender,
              })(
                <div>
                <Button name="parent_gender" 
                  value="female"
                  onChange={e => this.handleChange({target: {parent_gender: 'female'}})}>
                  F
                </Button>
                <Button name="parent_gender" 
                  value="male"
                  onChange={e => this.handleChange({target: {parent_gender: 'male'}})}>
                  M
                </Button>
                </div>
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('parent_birthdate', {
                rules: [{ required: true, message: 'Please input your birthdate!' }],
                initialValue: this.state.parent_birthdate,
              })(
                <Input name='parent_birthdate'
                  onChange={this.handleChange}
                  prefix={<Icon type='clock-circle' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='보호자 생년월일' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('phone', {
                rules: [{ required: true, message: 'Please input your phone number!' }],
                initialValue: this.state.phone,
              })(
                <Input name='phone'
                  onChange={this.handleChange}
                  prefix={<Icon type='phone' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='휴대전화' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('address', {
                rules: [{ required: true, message: 'Please input your address!' }],
                initialValue: this.state.address,
              })(
                <Input name='address'
                  onChange={this.handleChange}
                  prefix={<Icon type='environment' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='주소' />
              )}
            </Form.Item>
          </Form>
        }

        {this.state.current_step === 2 &&
          <Form>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('child_name', {
                rules: [{ required: true, message: 'Please input your name!' }],
                initialValue: this.state.child_name,
              })(
                <Input name='child_name'
                  onChange={this.handleChange}
                  prefix={<Icon type='user' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='아이 이름' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('child_gender', {
                rules: [{ required: true, message: 'Please input your gender!' }],
                initialValue: this.state.child_gender,
              })(
                <Input name='child_gender'
                  onChange={this.handleChange}
                  prefix={<Icon type='' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='성별' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('child_birthdate', {
                rules: [{ required: true, message: 'Please input your birthdate!' }],
                initialValue: this.state.child_birthdate,
              })(
                <Input name='child_birthdate'
                  onChange={this.handleChange}
                  prefix={<Icon type='clock-circle' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='생년월일' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('child_height', {
                rules: [{ required: true, message: 'Please input your height!' }],
                initialValue: this.state.child_height,
              })(
                <Input name='child_height'
                  onChange={this.handleChange}
                  prefix={<Icon type='' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='신장' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('child_weight', {
                rules: [{ required: true, message: 'Please input your weight!' }],
                initialValue: this.state.child_weight,
              })(
                <Input name='child_weight'
                  onChange={this.handleChange}
                  prefix={<Icon type='' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='체중' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('child_developmental_age', {
                rules: [{ required: true, message: 'Please input developmental age of the child!' }],
                initialValue: this.state.child_developmental_age,
              })(
                <Input name='child_developmental_age'
                  onChange={this.handleChange}
                  prefix={<Icon type='' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='발달연령' />
              )}
            </Form.Item>
            <Form.Item style={{ margin: '0 0 16px 0' }}>
              {getFieldDecorator('child_remark', {
                rules: [{ required: true, message: 'Please input your weight!' }],
                initialValue: this.state.child_remark,
              })(
                <Input name='child_remark'
                  onChange={this.handleChange}
                  prefix={<Icon type='' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  placeholder='특이사항' />
              )}
            </Form.Item>
            
            {loggingIn &&
              <Spin />
            }
          </Form>
        }
 
        <Row type="flex" justify="space-between" style={{ margin: '0 0 12px 0' }}>
          <Col span={6}>
            <Link to="/login">
              <Button type="danger" onClick={() => {}}>
                Back
              </Button>
            </Link>
          </Col>
          {this.state.current_step > 0 &&
          <Col span={6}>
            <Button onClick={() => this.setState({current_step: this.state.current_step - 1})}>
              Prev
            </Button>
          </Col>
          }
          {this.state.current_step < 2 &&
          <Col span={6}>
            <Button onClick={() => this.setState({current_step: this.state.current_step + 1})}>
              Next
            </Button>
          </Col>
          }
          {this.state.current_step === 2 &&
          <Col span={6}>
            <Button type="primary" onClick={this.handleSubmit}>
              Submit
            </Button>
          </Col>
          }
        </Row>
      </div>
    )
  }
}

function mapStateToProps(state) {
  const { loggingIn } = state.authentication
  return {
    loggingIn
  }
}

const connectedRegisterForm = connect(mapStateToProps)(RegisterForm)
export { connectedRegisterForm as RegisterForm }
