import { matchConstants } from '../_constants'

export function match(state = {}, action) {
  switch (action.type) {
  case matchConstants.GET_MATCH_REQUEST:
    return {
      loading: true
    }
  case matchConstants.GET_MATCH_SUCCESS:
    return {
      items: action.match
    }
  case matchConstants.GET_MATCH_FAILURE:
    return {
      error: action.error
    }
  default:
    return state
  }
}
