import React from 'react'
import { connect } from 'react-redux'

import { 
  Form,  
  Input,
} from 'antd'

const formItemLayout = {
	labelCol: {
		xs: { span: 24 },
		sm: { span: 6 },
	},
	wrapperCol: {
		xs: { span: 24 },
		sm: { span: 18 },
	},
};


class Personal extends React.Component {

  constructor(props) {
    super(props)
    this.state = {}
    this.handleChange = this.handleChange.bind(this)
  }

  handleChange(e) {
    const { name, value } = e.target
    this.setState({ [name]: value }, () => {
      this.props.setData({
        ...this.props.data,
        ...this.state,
      })
    })
  }

  render() {
    const { 
      getFieldDecorator, 
    } = this.props.form;
    
    return (
      <Form {...formItemLayout} >
        <Form.Item label="이메일" >
          {getFieldDecorator('email', {
            rules: [
              { required: true, message: "이메일 주소를 입력해주세요." },
              { type: "email", message: "올바른 이메일 형식이 아닙니다."}
            ]
          })( <Input name="email" onChange={this.handleChange}/> )}
        </Form.Item>
        <Form.Item label="비밀번호" >
          {getFieldDecorator('password', {
            rules: [
              { required: true, message: "비밀번호를 입력하세요."}
            ]
          })( <Input.Password name="password" onChange={this.handleChange}/> )}
        </Form.Item>
        <Form.Item label="비밀번호 확인" >
          {getFieldDecorator('confirm_password', {
            rules: [
              { required: true, message: "비밀번호를 다시 입력하세요."}
            ]
          })( <Input.Password name="password_confirm" onChange={this.handleChange}/> )}
        </Form.Item>
      </Form>
    )
  }
}

const WrappedPersonalForm = Form.create({ name: 'personal' })(Personal);

const mapStateToProps = (state) => {
  return {
  }
}

const connectedPersonal = connect(mapStateToProps, {
})(WrappedPersonalForm)

export { connectedPersonal as Personal }
