import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import classNames from 'classnames/bind'
import moment from 'moment'
import styles from './MainSider.scss'
import { Link } from 'react-router-dom'
import { authHeader } from '../_helpers'
import { config } from '../config'
import  {
  Avatar,
  Button,
	Comment,
  Divider,
  Empty,
  DatePicker,
  Icon,
} from 'antd'

const cx = classNames.bind(styles)

const getRandomColor = () => {
  var letters = '0123456789ABCDEF';
  var color = '#';
  for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
  return color;
}

const ChildTemplate = ({phone, childName, childAge, childGender}) => {
  return (
		<Comment
      className={cx('childTemplate')}
			author={childName}
			avatar={
	      <Avatar style={{ backgroundColor: getRandomColor(), verticalAlign: 'middle' }} size="large">
        </Avatar>
			}
			content={
				<p>
          {childAge} / {childGender} 
          <br />
          {phone}
				</p> 
			}
	  />
  )
}

const InstructorTemplate = ({phone, instructorName, instructorAge, instructorGender}) => {
  return (
		<Comment
      classname={cx('instructorTemplate')}
			author={instructorName}
			avatar={
        <Avatar style={{ backgroundColor: getRandomColor(), verticalAlign: 'middle' }} size="large">
        </Avatar>
      }
			content={
				<p>
          {instructorAge} / {instructorGender} 
          <br />
          {phone}
				</p> 
			}
	  />
  )
}

const MainSider = (props) => {
  // 메인 화면에  hover되어 있는 사이드바  
  const [date, setDate] = useState(moment())
  const [Members, setMembers] = useState([]) 
  const [MemberDiv, setMemberDiv] = useState(() => <div />)
  const [Place, setPlace] = useState(() => <div />)
  const [Instructor, setInstructor] = useState(() => <div />)
  
  const requestOptions = {
    method: "GET",
    headers: authHeader(),
  } 

  useEffect(() => {
    if (typeof props.idx === "number") {
      const n = Math.floor(Math.random() * 3) + 2
      fetch(`${config.apiUrl}/api/user/team/${props.idx}/${n}`, requestOptions)
        .then(response => response.json())
        .then(response => response.data)
        .then(data => {
          setMembers(data)
        })
        .catch(e => console.log(e))
    } else {
      setMembers([])
    }
  }, [props.idx])

  useEffect(() => {
    setMemberDiv(
      (
        Members.length > 0 ? (
          <React.Fragment>
            {Members.map(({childDevelopmentAge, childName, childGender, phone}) => (
              <ChildTemplate 
                childAge={childDevelopmentAge}
                childName={childName}
                childGender={childGender}
                phone={phone}
                />
            ))}
            <Button type="primary" block>
              팀 합류하기
            </Button>
          </React.Fragment>
        ): (
          <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
        )
      )
    )
  }, [Members])

  useEffect(() => {
    setPlace(
      (
        <p>
          {props.address}
          <br />
          {props.addressDetail}
        </p>
      )
    )
  }, [props.address, props.addressDetail])
  
  useEffect(() => {
    setInstructor(
      (
        <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
      )
    )
  }, [])


  const renderDate = (current) => {
    return (
      <p>
        {current.date()}
      </p>
    )
  }

  return (
    <div className={cx('main-sider')}>
      <div className={cx('main-datepicker')}>
        <DatePicker onChange={setDate}
          dateRender={renderDate}
          defaultValue={date}
        />
        <Link to="/settings/personal">
          <Button type="ghost">
            <Icon type="setting" />
          </Button>
        </Link>
      </div>

      <div className={cx('main-members')}>
        <h3>그룹 멤버</h3>
        {MemberDiv}
     </div>
      <Divider />
      <div className={cx('main-instructor')}>
        <h3>인솔자</h3>
        {Instructor}
     </div>
      <Divider />
      <div className={cx('main-meeting-place')}>
        <h3>모임 장소</h3>
        {Place}
     </div>
    </div>
  )
}

function mapStateToProps(state) { 
  const { match } = state;
  return {
    match,
  }
}

const connectedMainSider = connect(mapStateToProps)(MainSider)
export { connectedMainSider as MainSider }
