import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import classNames from 'classnames/bind'
import moment from 'moment'
import styles from './MainSider.scss'
import { Link } from 'react-router-dom'
import  {
  Avatar,
  Divider,
  Button,
  Comment,
  DatePicker,
  Icon,
  Radio,
  Row, 
  Col,
} from 'antd'

const cx = classNames.bind(styles)

const getRandomColor = () => {
  var letters = '0123456789ABCDEF';
  var color = '#';
  for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
  return color;
}

const ChildTemplate = ({phone, childName, childAge, childGender}) => {
  return (
		<Comment
      className={cx('childTemplate')}
			author={childName}
			avatar={
	      <Avatar style={{ backgroundColor: getRandomColor(), verticalAlign: 'middle' }} size="large">
        </Avatar>
			}
			content={
				<p>
          {childAge} / {childGender} 
          <br />
          {phone}
				</p> 
			}
	  />
  )
}

const TBITemplate = ({Div}) => {
  const [show, setShow] = useState(false)

  return show ? (
    <Div />
  ) : (
    <Row onClick={() => setShow(true)}>
      <Col span={3}>
      <Icon type="plus"/>
      </Col>
      <Col span={18}>
      <h3>합류 가능</h3>
      </Col>
    </Row>
  )
}



const CreateRoomSider = (props) => {
  // 메인 화면에  hover되어 있는 사이드바  
  const [date, setDate] = useState(moment())
  const [Place, setPlace] = useState(<div />) 
  
  useEffect(() => {
    setPlace(
      (
        <p>
          {props.address}
          <br />
          {props.addressDetail}
        </p>
      )
    )
  }, [props.address, props.addressDetail])
 
  const renderDate = (current) => {
    return (
      <p>
        {current.date()}
      </p>
    )
  }

  return (
    <div className={cx('main-sider')}>
      <div className={cx('main-datepicker')}>
        <DatePicker onChange={setDate}
          dateRender={renderDate}
          defaultValue={date}
        />
        <Link to="/settings/personal">
          <Button type="ghost" icon="setting"/>
        </Link>
      </div>
      <Row style={{ padding: '4px' }}>
        <Col span={6}>
          <h3>기간</h3>
        </Col>
        <Radio.Group buttonStyle="solid">
          <Radio.Button value="7">7일</Radio.Button>
          <Radio.Button value="14">14일</Radio.Button>
          <Radio.Button value="28">28일</Radio.Button>
        </Radio.Group>
      </Row>
      <Row style={{ padding: '4px' }}>
        <Col span={6}>
          <h3>시간</h3>
        </Col>
        <Radio.Group buttonStyle="solid">
          <Radio.Button value="am">오전</Radio.Button>
          <Radio.Button value="pm">오후</Radio.Button>
          <Radio.Button value="all">종일</Radio.Button>
        </Radio.Group>
      </Row>
      <Divider />
      <h3>참여자 목록</h3>
      <div>
        <ChildTemplate 
          childName="김서윤"
          childAge="5"
          childGender="여"
          phone="010-1234-5678"
        />
        <TBITemplate Div={() => (
           <ChildTemplate 
            childName="김서윤"
            childAge="5"
            childGender="여"
            phone="010-1234-5678"
          />
        )}/>
        <TBITemplate Div={() => (
           <ChildTemplate 
            childName="김서윤"
            childAge="5"
            childGender="여"
            phone="010-1234-5678"
          />
        )}/>
        <TBITemplate Div={() => (
           <ChildTemplate 
            childName="김서윤"
            childAge="5"
            childGender="여"
            phone="010-1234-5678"
          />
        )}/>
      </div>
      <Divider />
      <h3>선생님</h3>
      <div>
        <TBITemplate Div={() => (
            <div />
        )}/> 
      </div>
      <Divider />
      <h3>모임 장소</h3>
      <div>
        { Place }
      </div>
      <Button type="primary" block>
        모임 만들기
      </Button>
    </div>
  )
}

function mapStateToProps(state) { 
  const { match } = state;
  return {
    match,
  }
}

const connectedCreateRoomSider = connect(mapStateToProps)(CreateRoomSider)
export { connectedCreateRoomSider as CreateRoomSider }
