import React from 'react'
import { connect } from 'react-redux'

import { 
  Form,  
  Input,
  Radio,
  DatePicker,
} from 'antd'

const formItemLayout = {
	labelCol: {
		xs: { span: 24 },
		sm: { span: 6 },
	},
	wrapperCol: {
		xs: { span: 24 },
		sm: { span: 18 },
	},
};


class Instructor extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      address: undefined,
      _phone1: "",
      _phone2: "",
      _phone3: "",
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleChangeDate = this.handleChangeDate.bind(this)
    this.handlePhone = this.handlePhone.bind(this)
    this.handleAddress = this.handleAddress.bind(this)
  }

  handleChange(e) {
    const { name, value } = e.target
    this.setState({ [name]: value }, () => {
      this.props.setData({
        ...this.props.data,
        ...this.state,
      })
    })
  }
  
  handleChangeDate(name, moment) {
    const day = moment.date()
    const month = moment.month() + 1
    const year = moment.year()
    
    this.setState({ [name]: `${year}-${month}-${day}` }, () => {
      this.props.setData({
        ...this.props.data,
        ...this.state,
      })
    })
  }

  handlePhone(e) {
    const { name, value } = e.target
    this.setState({ [name]: value }, () => {
      const { _phone1, _phone2, _phone3 } = this.state
      const phone = _phone1 + _phone2 + _phone3
       this.setState({ phone }, () => {
        this.props.setData({
          ...this.props.data,
          ...this.state,
        })
      })
    })
    console.log(name, value)
    // tbi
  }
  
  handleAddress(a) {
    console.log(a)
    this.setState({ address: a.address }, () => {
       this.props.setData({
        ...this.props.data,
        ...this.state,
      })
    })
  }

  render() {
    const { 
      getFieldDecorator,
      setFieldsValue,
    } = this.props.form;
    
    return (
      <Form {...formItemLayout} >


        <Form.Item label="선생님 이름" >
          {getFieldDecorator('instructorName', {
            rules: [
              { required: true, message: "선생님 이름을 입력해주세요." },
            ]
          })( <Input name="instructorName" onChange={this.handleChange}/> )}
        </Form.Item>
        
        
        <Form.Item label="성별" >
          {getFieldDecorator('instructorGender', {
            rules: [
              { required: true, message: "성별을 입력하세요."}
              ]
          })( 
            <Radio.Group name="instructorGender" buttonStyle="solid" onChange={this.handleChange}>
              <Radio.Button value="female">F</Radio.Button>
              <Radio.Button value="male">M</Radio.Button>
            </Radio.Group>
          )}
        </Form.Item>
        <Form.Item label="생년월일" >
          {getFieldDecorator('instructorBirthDate', {
            rules: [
              { type: 'object', required: true, message: "생년월일을 입력하세요."}
            ]
          })( <DatePicker onChange={m => this.handleChangeDate("instructorBirthDate", m)}/> )}
        </Form.Item>

        <Form.Item label="경력" >
          {getFieldDecorator('instructorCareer', {
            rules: [
              { required: true, message: "경력을 입력해주세요." },
            ]
          })( <Input name="instructorCareer" onChange={this.handleChange}/> )}
        </Form.Item>

        <Form.Item label="자격증 유무">
          {getFieldDecorator('instructorCertificate', {
            rules: [
              { required: true, message: "자격증 소지 여부를 입력해주세요." },
            ]
          })(
            <Radio.Group>
              <Radio.Button value={true}>예</Radio.Button>
              <Radio.Button value={false}>아니오</Radio.Button>
            </Radio.Group>
          )}
        </Form.Item>
 
       <Form.Item label="주소">
           {getFieldDecorator('address', {
            rules: [
              { required: true, message: "주소를 입력해주세요." },
            ]
          })( 
          <Input name="address"  
            onClick={() => {
              new window.daum.Postcode({
                oncomplete: (data) => {
                  this.handleAddress(data)
                  setFieldsValue({ 'address': data.address })
                  // 주소 획득
                  const geocoder = new window.daum.maps.services.Geocoder()
                  geocoder.addressSearch(data.address, (res, status) => {
                  if (status === window.daum.maps.services.Status.OK) {
                    //const coord = new window.daum.maps.LatLng(res[0].y, res[0].x)
                    console.log(res[0].y, res[0].x)
                  }
                  })
                }
              }).open()
            }}/> )}
        </Form.Item>
       <Form.Item label="상세주소">
           {getFieldDecorator('addressDetail', {
            rules: [
              { required: true, message: "상세주소를 입력해주세요." },
            ]
          })( 
          <Input name="addressDetail" onChange={this.handleChange} /> )}
        </Form.Item>
      </Form>
    )
  }
}

const WrappedInstructorForm = Form.create({ name: 'instructor' })(Instructor);

const mapStateToProps = (state) => {
  // TBI : initialValue 추가
  return {
    
  }
}

const connectedInstructor = connect(mapStateToProps, {
})(WrappedInstructorForm)

export { connectedInstructor as Instructor }
