/*global kakao*/
import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { MainSider, CreateRoomSider } from './'
import classNames from 'classnames/bind'
import styles from './GroupPage.module.scss'
import { config } from '../config'
import { cafe_list } from './cafe'

const cx = classNames.bind(styles)

const GroupPage = (props) => {
  const [map, setMap] = useState()
  const [idx, setIdx] = useState()
  const [address, setAddress] = useState()
  const [addressDetail, setAddressDetail] = useState()
  
  const [createRoom, setCreateRoom] = useState(false)

  const pos_y = 37.4127
  const pos_x = 127.12524
  useEffect(() => {
    const mapDiv = document.getElementById('map')
    const mapOption = {
      center: new kakao.maps.LatLng(pos_y, pos_x), // 지도의 중심좌표
      level: 4 // 지도의 확대 레벨
    }
    setMap(new kakao.maps.Map(mapDiv, mapOption))
  }, [])

  useEffect(() => {
    const radius = 1000
    const center = new kakao.maps.LatLng(pos_y, pos_x)
    
    const circle = addCircle(map, center, radius)
    const centerMarker = addCenterMarker(map, center)

    cafe_list.forEach((obj, index) => {
      addMeetingPointCandidateMarker(map, 
        new kakao.maps.LatLng(obj.y, obj.x), index, obj)
    })
    /*
    const DUMarker = addMeetingPointMarker(map, 
      new kakao.maps.LatLng(pos_y - 0.0044, pos_x - 0.005)
    )*/
  }, [map])

  const addCircle = (map, center, radius) => {
 		const circle = new kakao.maps.Circle({
			center,  // 원의 중심좌표 입니다 
			radius, // 미터 단위의 원의 반지름입니다
      strokeWeight: 2, // 선의 두께입니다 
			strokeColor: '#ababab', // 선의 색깔입니다
			strokeOpacity: 1, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
			// strokeStyle: 'dashed', // 선의 스타일 입니다
			fillColor: '#fff', // 채우기 색깔입니다
      fillOpacity: 0.2  // 채우기 불투명도 입니다   
		}); 
		circle.setMap(map); 
    return circle
  }

  const addCenterMarker = (map, center) => { 
    const markerImage = new kakao.maps.MarkerImage(
      `${config.imageUrl}/static/assets/marker-address.png`, 
      new kakao.maps.Size(60, 80), 
      { offset: new kakao.maps.Point(30, 70) }
    )
    const marker = new kakao.maps.Marker({
      position: center,
      image: markerImage,
    })
    marker.setMap(map)
    kakao.maps.event.addListener(marker, 'mouseover', function() {
      setCreateRoom(true)
    });
    return marker
  }
  
  const addMeetingPointCandidateMarker = (map, center, index, data) => {
    const markerImage = new kakao.maps.MarkerImage(
      `${config.imageUrl}/static/assets/marker-meeting-point.png`, 
      new kakao.maps.Size(60, 80), 
      { offset: new kakao.maps.Point(30, 70) }
    )
    const marker = new kakao.maps.Marker({
      position: center,
      image: markerImage,
      opacity: 0.7
    })
    marker.setMap(map)
    kakao.maps.event.addListener(marker, 'mouseover', function() {
      marker.setOpacity(1.0)
      setIdx(index)
      setCreateRoom(false)
      setAddress(data.address_name)
      setAddressDetail(data.place_name)
    });
    kakao.maps.event.addListener(marker, 'mouseout', function() {
      marker.setOpacity(0.7)
    })
   return marker
  }
  
  const showInfo = (map) => {
    if (!map) return;
    var center = map.getCenter(); 
    var level = map.getLevel();
    return {center, level}
  }

  return (
    <div className={cx('mainContainer')}>
      {
        createRoom ? 
        <CreateRoomSider address="경기도 성남시 분당구 야탑동 345-2" 
          addressDetail="베네피아 600호"/>
        :
        <MainSider idx={idx} address={address} addressDetail={addressDetail} />
      }
      <div id="mapContainer" className={cx('mapContainer')}>
        <div id="map"
          onScroll={showInfo}
          style={{
            height: window.innerHeight - 48,
            width: window.innerWidth
          }}
        />
			</div>
    </div>
  )
}

function mapStateToProps(state) {
  return {}
}

const connectedGroupPage = connect(mapStateToProps, {
})(GroupPage)

export { connectedGroupPage as GroupPage }
